<template>
  <div class="addApi">
    <topNav title="添加连接" url="/settingApiCloudDining"></topNav>
    <div class="addTop">
      <div class="addTopBox">
        <div class="box" :class="{active:(addType==1)}" @click="addType=1">
          <h2>根据代码添加</h2>
          <span></span>
        </div>
        <div class="box" :class="{active:(addType==2)}" @click="addType=2">
          <h2>手工添加</h2>
          <span></span>
        </div>
      </div>
    </div>
    <div class="hotelCode" v-if="addType==1">
      <div class="hotels">
        <div class="hotelBox">
          <span>酒店代码</span>
          <input
            type="text"
            class="hotelInput"
            style="text-transform: uppercase;"
            v-model="hotelData.hotelCode"
          >
        </div>
      </div>
    </div>
    <div class="hotelCode" v-else>
      <div class="codeBox">
        <div class="hotelBox">
          <span>连接名称</span>
          <input type="text" class="hotelInput" v-model="hotelData.name">
        </div>
        <div class="hotelBox">
          <span>酒店名称</span>
          <input type="text" class="hotelInput" v-model="hotelData.hotelName">
        </div>
        <div class="hotelBox">
          <span>集团代码</span>
          <input
            type="text"
            class="hotelInput"
            style="text-transform: uppercase;"
            v-model="hotelData.hotelGroupCode"
          >
        </div>
        <div class="hotelBox">
          <span>酒店代码</span>
          <input
            type="text"
            class="hotelInput"
            style="text-transform: uppercase;"
            v-model="hotelData.hotelCode"
          >
        </div>
        <div class="hotelBox">
          <span>连接地址</span>
          <input type="text" class="hotelInput" v-model="hotelData.url">
        </div>
      </div>
    </div>
    <div class="button_box">
      <div class="addButton" @click="add">添加</div>
    </div>
  </div>
</template>

<script>
import { Indicator, Toast } from "mint-ui";
export default {
  data() {
    return {
      addType: 1,
      hotelData: {
        name: "",
        hotelName: "",
        hotelGroupCode: "",
        hotelCode: "",
        url: "http://"
      },
      url: "/settingApi"
    };
  },
  mounted() {},
  methods: {
    add() {
      if (this.hotelData.hotelCode == "") {
        this.msg("酒店代码不能为空");
        return;
      }
      if (this.addType == 1) {
        Indicator.open();
        var url =
          "http://api.ihotel.cn/s/url/" +
          this.hotelData.hotelCode.toUpperCase() +
          "_MOBILEPOS";
        console.log(url);
        this.$http.get(url, { timeout: 10000 }).then(res => {
          console.log(res.data);
          if (res.data.code == 0) {
            var hd = res.data.data[0];
            var ss = hd.url.split(";");
            if (ss.length < 3) {
              this.msg("地址服务配置错误");
              Indicator.close();
              return;
            }
            this.hotelData.name = hd.name;
            this.hotelData.hotelName = ss[1];
            this.hotelData.hotelGroupCode = ss[2];
            this.hotelData.url = ss[0];
            Indicator.close();
            this.save();
          } else {
            this.msg(res.data.msg);
            Indicator.close();
          }
        });
      } else {
        if (
          this.hotelData.url.toUpperCase().indexOf("HTTP://") == -1 &&
          this.hotelData.url.toUpperCase().indexOf("HTTPS://") == -1
        ) {
          this.msg("不是合法的URL");
          return;
        }
        this.save();
      }
    },
    save() {
      if (this.hotelData.name == "") {
        this.msg("连接名称不能为空");
        return;
      }
      if (this.hotelData.hotelName == "") {
        this.msg("酒店名称不能为空");
        return;
      }
      if (this.hotelData.hotelGroupCode == "") {
        this.msg("集团代码不能为空");
        return;
      }
      if (this.hotelData.url == "") {
        this.msg("地址不能为空");
        return;
      }
      this.hotelData.hotelCode = this.hotelData.hotelCode.toUpperCase();
      this.hotelData.hotelGroupCode = this.hotelData.hotelGroupCode.toUpperCase();
      var oldAdd = localStorage.getItem("address");
      if (oldAdd == null) {
        localStorage.setItem("address", JSON.stringify(this.hotelData));
      } else {
        var nadd = JSON.stringify(this.hotelData);
        if (oldAdd.indexOf(nadd) >= 0) {
          this.msg("此地址已经添加");
          return;
        }
        localStorage.setItem("address", oldAdd + "|" + nadd);
      }
      localStorage.setItem("gateway", JSON.stringify(this.hotelData));
      this.msg("添加成功");

      this.$router.push({ path: "/settingApiCloudDining" });
    },

    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    }
  }
};
</script>
<style lang="less">
.addApi {
  .addTop {
    width: 100%;
    height: 0.9867rem;
    background: #ffffff;
    .addTopBox {
      width: 80%;
      height: 0.9867rem;
      margin: 0 auto;
      .box {
        width: 49%;
        text-align: center;
        height: 0.9867rem;
        display: inline-block;
        h2 {
          font-size: 0.32rem;
          color: #333333;
          display: block;
          line-height: 0.9333rem;
        }
        span {
          margin: 0 auto;
          width: 30%;
          height: 0.0587rem;
          background: #646f83;
          display: none;
        }
      }
      .active {
        span {
          display: block;
        }
      }
    }
  }
  .hotelCode {
    width: 96%;
    background: #ffffff;
    margin: 0 auto;
    margin-top: 0.4rem;
    height: auto;
    box-shadow: 0 0 0.2133rem 0rem #dadada;
    border-radius: 0.1333rem;
    .codeBox {
      padding-top: 0.1333rem;
      padding-bottom: 0.5333rem;

      .hotelBox {
        width: 100%;
        height: 0.9867rem;
        margin-top: 0.3467rem;
        span {
          display: inline-block;
          width: 21%;
          margin-top: 0.1333rem;
          float: left;
          margin-left: 0.4rem;
          font-size: 0.3733rem;
        }
        .hotelInput {
          border: 0.0267rem solid #cccccc;
          border-radius: 0.1333rem;
          padding-left: 0.1333rem;
          font-size: 0.4267rem;
          width: 64%;
          height: 0.9867rem;
        }
      }
    }
    .hotels {
      padding-bottom: 0.8rem;
      padding-top: 0.72rem;
      width: 100%;
      height: 0.9867rem;
      .hotelBox {
        width: 100%;
        height: 0.9867rem;
        span {
          display: inline-block;
          width: 21%;
          margin-top: 0.1333rem;
          float: left;
          margin-left: 0.4rem;
          font-size: 0.3733rem;
        }
        .hotelInput {
          border: 0.0267rem solid #cccccc;
          border-radius: 0.1333rem;
          padding-left: 0.1333rem;
          font-size: 0.4267rem;
          width: 64%;
          height: 0.9867rem;
        }
      }
    }
  }
  .button_box {
    width: 100%;
    height: 2rem;
    .addButton {
      width: 36%;
      height: 0.9867rem;
      text-align: center;
      line-height: 0.9067rem;
      color: #fff;
      font-size: 0.3733rem;
      background: #ff8902;
      border-radius: 0.1867rem;
      display: inline-block;
      margin: 0.4rem auto 0.48rem;
    }
  }
}
</style>



